body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navbar{
  background-color:#f8f9fa !important;
}
.navbar-brand {
  font-size: 2rem !important;
  color: #0b0e0e !important;
}

.navbar a{
  font-size: 1.3rem !important;
  text-transform: capitalize !important;
}
.menu_active{
  font-weight: bold !important;
  border-bottom: 1px solid rgb(255, 6, 160) !important;
}
.navbar a:hover{
  color: rgb(238, 137, 154)  !important;
}
@media (max-width: 991px){
  
.menu_active{
  font-weight: bold;
  border-bottom: none;
}
}

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

header h1{
  margin:0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name {
  color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 24px;
}

.paraclass{
  font-size: 18px;
    font-style: oblique;
    color: darkolivegreen;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  background-color: pink;
  border: 2px solid rgb(248, 78, 106);
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: rgb(255, 144, 160);
  color: #fff;
}
.header-img{
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.height{
  min-height: 70vh;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  } 
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  } 
  100%{
    transform: translateY(-10px);
  }
}
footer p{
  padding: 0;
  margin-top: auto;
  bottom: 0;
}
@media (max-width: 991px){
  
  #header{
   height: 80vh;
   text-align: center;
  }

  #header .header-img{
    text-align: center;
  }
  #header .header-img img{
    width: 60%;
  }
  }

  @media (max-width:768px){
    #header{
      margin-top: 20px;
    }
    #header h1{
      font-size: 28px;
    }
    #header h2{
      font-size: 18px;
      margin-bottom: 30px;
    }
    #header .header-img img{
      width: 70%;
    }
  }
  
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
